@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
html {
  padding: 0;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  background-color: #1f1b24;
  -ms-overflow-style: none;
  overflow-y: scroll;
  scrollbar-width: none;
}

html::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  height: 30px;
  padding: 1vh 0;

  background-color: #131313;
  align-items: center;

  position: -webkit-sticky;

  position: sticky;
  top: 0;
  z-index: 100;
}

.nav-list {
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  text-align: center;

  list-style: none;
}

.nav-list * {
  width: 33%;
  margin: auto;
}

.nav-list *:hover {
  color: #fff;
}

.Weather {
  text-align: center;
}

.dayPropertyItem {
  width: 30%;
  height: 100px;
  margin-bottom: 10%;
}

.dayPropertyItem p:first-child {
  font-size: 30px;
  font-style: bold;

  margin-bottom: 0;
}

.dayCard {
  height: 75vh;
  margin: 10vh 5vw;
  padding: 5vh;

  scroll-snap-align: center;
  scroll-snap-stop: always;

  display: flex;
  flex-direction: row;
  justify-content: space-around;

  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 60px;

  font-family: 'Montserrat', sans-serif;
  color: #fff;

  z-index: 5;
}

.card  {
  display: flex;
  flex-direction: column;
  width: 40%;
  padding: 60px;
  text-align: left;
}

.cardData {
  font-size: 20px;
}

.dayCard h1 {
font-size: 4rem;
letter-spacing: 0.5rem;
}

.dayCard h1, .dayTempMain {
  margin: 0;
  padding: 0;
  font-size: 50px;
}

.dayMainInfo {
  display: flex;
  flex-direction: row;
  grid-gap: 40px;
  gap: 40px;

  margin: auto 0;
}

.cardDescription {
  text-transform: uppercase;
  font-size: 15px;
}

.dayProperties {
  width: 60%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-self: center;

  margin: auto;
}

.videoTag {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: -99;
}

@media (min-width: 820px) {
  .Retro {
    padding: 20px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    grid-gap: 10px;
    gap: 10px;
    background-color: #1f1b24;
    height: 100%;
    overflow: hidden;
  }
}

@media (max-width: 820px) {
  .Retro {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    grid-gap: 10px;
    gap: 10px;
  }
}

.container {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;

  padding: 30px;

  width: 30%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-height: 75vh;

  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;

  font-family: "Ubuntu", sans-serif;
}

.card-container {
  padding: 5px;

  -ms-overflow-style: none;
  overflow-y: scroll;
  scrollbar-width: none;

  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  grid-gap: 10px;
  gap: 10px;
}

.card-container::-webkit-scrollbar {
  display: none;
}

.container-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.container-top h1 {
  margin: 0;
  color: #c4c4c4;
  font-size: 160%;
}

.card-count {
  margin: auto 0;
  padding: 1px 6px 2px 6px;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
  height: 20px;

  border: 1px solid #c4c4c4;
  border-radius: 5px;

  color: #fff;
}

.add-card {
  outline: none;
  border: none;
  border-radius: 10px;

  padding: 10px;

  background-color: rgba(255, 255, 255, 0.1);
  color: #c4c4c4;
  font-family: "Ubuntu", sans-serif;
}

.add-card:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.2);
}

@media (max-width: 820px) {
  .container {
    width: 85%;
    margin: auto;
  }
}

.rating-container {
  display: flex;
  flex-direction: row;
  grid-gap: 2px;
  gap: 2px;
}

.rating-container p {
  font-size: 1vw;
  color: #c4c4c4;
}

.retro-card-like, .retro-card-dislike {
  border: none;
  outline: none;
  background: none;

  margin: auto 0;
  padding: 3px 6px;

  display: flex;

  color: #e7e7e7;
}

.retro-card-like:hover, .retro-card-dislike:hover {
  cursor: pointer;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.2);
}

.retro-card-dislike > img, .retro-card-like > img {
  width: 1.2vw;
}

.retro-card-dislike > img {
  margin-top: 2px;
}

@media(max-width: 820px) {
  .rating-container p {
    font-size: 0.8rem;
  }

  .retro-card-dislike > img, .retro-card-like > img {
    width: 15px;
  }
}

.retroCard {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 90%;

  padding: 5%;
  padding-bottom: 0;

  border: 2px solid;
  border-radius: 10px;

  color: #fff;
}

.retro-card-content {
  word-break: break-word;
  text-align: left;
}

.retro-card-options {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  bottom: 0;
}

.retro-card-date {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  grid-gap: 15px;
  gap: 15px;

  font-size: 0.9vw;

  bottom: 0;
  margin: auto 0;

  color: #c4c4c4;
}

.retro-card-btns {
  display: flex;
  grid-gap: 0;
  gap: 0;
  bottom: 0;
}

.retro-card-delete {
  border: none;
  outline: none;
  background: none;

  margin: auto 0;
  padding: 3px 6px;

  display: flex;

  color: #e7e7e7;
}

.retro-card-delete:hover {
  cursor: pointer;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.2);
}

.retro-card-delete > img {
  width: 1.2vw;
}

@media (max-width: 820px) {
  .retro-card-date {
    font-size: 0.7rem;
  }

  .retro-card-btns p {
    font-size: 0.8rem;
  }

  .retro-card-delete > img {
    width: 15px;
  }
}

.newCard {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  height: -webkit-fit-content;

  height: -moz-fit-content;

  height: fit-content;
  width: 90%;

  padding: 5%;

  border: 2px solid #e7e7e7;
  border-radius: 10px;

  color: #e7e7e7;
  font-family: 'Ubuntu', sans-serif;
}

.new-card-input {
  outline: none;
  border: none;
  background: none;

  color: #e7e7e7;
  font-family: 'Ubuntu', sans-serif;
  font-size: 1rem;
}

.new-card-btn {
  outline: none;
  border: none;
  background: none;

  height: 25px;
  width: 25px;

  border: 2px solid #e7e7e7;
  border-radius: 50%;

  color: #e7e7e7;
  font-size: 1rem;
}

.new-card-btn:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
}

.TodoItem {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  align-self: center;

  height: -webkit-fit-content;

  height: -moz-fit-content;

  height: fit-content;
  width: 96%;

  padding: 10px;

  border: 2px solid rgba(231, 231, 231, 0.6);
  border-radius: 10px;

  color: #e7e7e7;
}

.TodoItem:nth-child(n) {
  border-color: #bb86fc;
}

.TodoItem:nth-child(2n) {
  border-color: #ff7597;
}

.TodoItem:nth-child(3n) {
  border-color: #02ddc5;
}

.todo-item-content {
  margin: 0;
  word-break: break-word;
  text-align: left;
}

.todo-item-delete {
  border: none;
  outline: none;
  background: none;

  margin: auto 0;
  padding: 4px 8px;

  display: flex;

  color: #e7e7e7;
}

.todo-item-delete:hover {
  cursor: pointer;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.2);
}

.Todos {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;

  align-items: flex-start;
  align-self: center;

  margin: 5vh auto;
  padding: 3vh;

  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);

  width: 84.5vw;
}

.todo-inputs {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.todo-inputs > * {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  height: -webkit-fit-content;

  height: -moz-fit-content;

  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

  border: 2px solid rgba(231, 231, 231, 0.6);
  border-radius: 10px;

  color: #e7e7e7;
}

.todo-inputs input,
.todo-inputs select {
  outline: none;
  border: none;
  background: none;

  margin: 10px;

  color: #e7e7e7;
  font-size: 1rem;
}

.todo-inputs select {
  border: 2px solid rgba(231, 231, 231, 0.6);
  border-radius: 10px;

  padding: 10px;
  margin: 0;
}

.todo-inputs select:hover {
  cursor: pointer;
}

.todo-inputs option {
  color: rgb(46, 46, 46);
}

.todo-inputs button {
  outline: none;
  border: none;
  background: none;

  color: #e7e7e7;
  font-size: 1rem;
}

.todo-inputs button:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
}

.App {
  text-align: center;
}

